.conteudo-principal-topo__container-bg {
  width: 100vw;
  height: 90vh;
  background: url("ramen-picante.bbd0e5e9.jpg") 0 0 / cover no-repeat;
}

.conteudo-principal-topo__container {
  height: 100%;
  width: 100%;
  box-sizing: border-box;
  background: var(--linear-preto-Mbranco);
  padding: 90px 50px 0;
}

.conteudo-principal-topo__container--caixa-texto {
  max-width: 30%;
  float: right;
}

.conteudo-principal-topo__container--texto {
  font-family: var(--fonte-padrao);
  font-size: 5em;
  font-weight: 400;
}

.conteudo-principal-topo__container--botao {
  background-color: rgba(0, 0, 0, 0);
  border-radius: 5px;
  margin-top: 3rem;
  padding: 15px;
  font-size: 1.25rem;
  transition: all .825s;
}

.conteudo-principal-topo__container--botao:hover {
  background-color: var(--botao-base);
  color: var(--branco);
}

@media screen and (max-width: 1024px) {
  .conteudo-principal-topo__container--caixa-texto {
    max-width: 70%;
    text-align: center;
    max-height: calc(100%-240px);
    margin: 0 15%;
  }

  .conteudo-principal-topo__container--texto {
    font-size: 5.25em;
  }

  .conteudo-principal-topo__container-bg {
    height: 60vh;
  }

  .conteudo-principal-topo__container {
    padding: 5vh 30px 0;
  }
}

@media screen and (max-width: 768px) {
  .conteudo-principal-topo__container--texto {
    font-size: 3.5em;
  }

  .conteudo-principal-topo__container {
    background: rgba(255, 255, 255, .68);
  }

  .conteudo-principal-topo__container--botao {
    margin-top: 2rem;
    padding: 10px;
    font-size: 1rem;
  }
}

